import React, { Component } from 'react';
import { bool, func, string, object } from 'prop-types';
import { connect } from 'react-redux';
import Modal from 'react-modal';
import Editor from './components/Editor';
// import Recipe from './components/Recipe';
import AddButton from './components/Buttons/AddButton';

import { setView, save, remove, toggleEdit, getRecipes } from './actions';


const mapStateToProps = ({ recipeBox }) => ({
  recipes: recipeBox.recipes,
  active: recipeBox.active,
  edit: recipeBox.edit,
});

const mapDispatchToProps = dispatch => ({
  // onToggleMenu: () => dispatch(toggleMenu()),
  onView: event => dispatch(setView(event.target.value)),
  // onEdit: values => dispatch(recipeEdit(values)),
  onRefresh: () => dispatch(getRecipes()),
  onEdit: () => dispatch(toggleEdit()),
  onDelete: event => dispatch(remove(event.target.value)),
  onSubmit: values => dispatch(save(values)),
});

class RecipeBox extends Component {
  componentDidMount() {
    this.props.onRefresh();
  }
  render() {
    const {
      onView,
      recipes,
      active,
      edit,
      onEdit,
      onDelete,
      onSubmit,
      cx,
    } = this.props;
    return (<div className={cx('recipe-box')}>
      {/* Modal */}
      <Modal isOpen={active || edit}>{
        (edit) ? (<Editor cx={cx} initialValues={active ? recipes[active] : {}} onSubmit={onSubmit} />) : (<div>
          {/* <Recipe cx={cx} name={recipes[active].name} picture={recipes[active].picture} ingredients={recipes[active].ingredients} onEdit={onEdit} onDelete={onDelete} />) */}
          recipe
        </div>)}</Modal>

      {/* List of recipes */}
      <div className={cx('recipe-box-menu')}>

        {/* Add button */}
        <AddButton cx={cx} onClick={onEdit} />
        <button onClick={onDelete}>remove</button>

        <ul role="menu" className={cx('recipe-box-menu__tiles')}>{
          Object.keys(recipes).map((recipe) => {
            const { _id, name, picture } = recipes[recipe];
            return (<li key={_id} value={_id} role="menuitem" className={cx('recipe-box-tile')} onClick={onView}>
              <div className={cx('recipe-box-tile__primary')}>
                <img className={cx('recipe-box-tile__primary-content')} src={picture} alt="" />
              </div>

              <span className={cx('recipe-box-tile__secondary')}>
                <span className={cx('recipe-box-tile__title')}>
                  {name}
                </span>
              </span>
            </li>);
          })
        }</ul>
      </div>
    </div>);
  }
}

RecipeBox.propTypes = {
  onRefresh: func.isRequired,
  onSubmit: func.isRequired,
  onView: func.isRequired,
  onEdit: func.isRequired,
  onDelete: func.isRequired,
  recipes: object.isRequired,
  active: string.isRequired,
  edit: bool.isRequired,
  cx: func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(RecipeBox);



